___CSS_LOADER_EXPORT___.push([module.id, `*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

body {
    background-color: antiquewhite;
    display: flex;
    justify-content: center;
}

.container{
    display: flex;
    width: 400px;
    height: 300px;
    flex-direction: column;
    background-color: white;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
    gap: 1rem;
}

h2 {
    color: rgb(83, 214, 214);
    font-weight: bold;
    
}

p {
    color: black;
    font-size: large;
}

.requests {
    display: flex;
    flex-direction: column;
}

button {
    border: .5px solid grey;
    border-radius: 10px;
    padding: 10px;
    background-color: rgb(195, 226, 216);
}

button:hover {
    cursor: pointer;
    background-color: rgb(188, 235, 235);
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,wEAAwE;AAC5E;;AAEA;IACI,8BAA8B;IAC9B,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,uBAAuB;IACvB,gBAAgB;IAChB,SAAS;AACb;;AAEA;IACI,wBAAwB;IACxB,iBAAiB;;AAErB;;AAEA;IACI,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;IACb,oCAAoC;AACxC;;AAEA;IACI,eAAe;IACf,oCAAoC;AACxC","sourcesContent":["*{\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;\r\n}\r\n\r\nbody {\r\n    background-color: antiquewhite;\r\n    display: flex;\r\n    justify-content: center;\r\n}\r\n\r\n.container{\r\n    display: flex;\r\n    width: 400px;\r\n    height: 300px;\r\n    flex-direction: column;\r\n    background-color: white;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin-top: 2rem;\r\n    gap: 1rem;\r\n}\r\n\r\nh2 {\r\n    color: rgb(83, 214, 214);\r\n    font-weight: bold;\r\n    \r\n}\r\n\r\np {\r\n    color: black;\r\n    font-size: large;\r\n}\r\n\r\n.requests {\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\nbutton {\r\n    border: .5px solid grey;\r\n    border-radius: 10px;\r\n    padding: 10px;\r\n    background-color: rgb(195, 226, 216);\r\n}\r\n\r\nbutton:hover {\r\n    cursor: pointer;\r\n    background-color: rgb(188, 235, 235);\r\n}"],"sourceRoot":""}]);
